	module.exports = {"data":{"markdownRemark":{"html":"<h3>About our terms and conditions</h3>\n<p>These terms of use (together with any documents referred to in them) explain how you may use this website, all associated web pages and any associated mobile applications or other software applications (together the \"Site\"), whether as a guest or a registered user. Use of our Site includes accessing, browsing, or registering to use our Site.</p>\n<p>By accessing or using this Site you agree to be bound by, and comply with, these terms and conditions and the documents referred to in them. If you do not agree with or accept any of these terms, you should stop using the Site immediately.</p>\n<p>We may update these terms from time to time in accordance with the \"Variation\" definition in the \"General legal terms\" section below.</p>\n<p>Your use of the Site means that you must also comply with our privacy and cookie policy which is available at <a href=\"http://doopoll.co/legals/privacy-policy\">http://doopoll.co/legals/privacy-policy</a> and, if you are purchasing services from us, our Services Terms and Conditions which are available at <a href=\"http://doopoll.co/legals/terms-of-service\">http://doopoll.co/legals/terms-of-service</a>.</p>\n<h3>About us</h3>\n<p>The Site is operated by Doopoll Ltd (\"we\", \"us\" or \"our\"). We are a company registered in England and Wales under company number 09676956 and have our registered office at Henstaff Court, Llantrisant Road, Groesfaen, Wales, CF72 8NG. References to us in these terms also include our group companies from time to time.</p>\n<h3>Using the Site and restrictions on use</h3>\n<p>We permit you to use the Site only in accordance with the terms of use set out here (and any documents referred to here). Use of the Site in any other way, including in contravention of any restriction on use set out in these terms of use, is not permitted. If you do not agree with the applicable terms you may not use the Site.</p>\n<p>You agree that you are solely responsible for all costs and expenses you may incur in relation to your use of the Site.</p>\n<p>The Site is intended for use only by those who can access it from within the UK. If you choose to access the Site from locations outside the UK, you are responsible for compliance with local laws where they are applicable.</p>\n<p>As a condition of your use of the Site, you agree:</p>\n<ul>\n<li>not to use the Site to carry out or promote any activity that is unlawful in any way under any applicable law; and</li>\n<li>not to use the site for any purpose that is prohibited by these terms of use.</li>\n</ul>\n<p>In addition, your right to use the Site does not permit you to attempt to decompile (as defined in section 50B of the Copyright, Designs and Patents Act 1988) the underlying software (or any part of it) that is used in or to provide the Site, or to observe, study or test the functioning of the underlying software (or any part of it) that is used in or to provide the Site, except and only to the extent that such restrictions are prohibited pursuant to section 50B of the Copyright, Designs and Patents Act 1988.</p>\n<p>We may prevent or suspend your access to the Site if you do not comply with any part of these Website terms and conditions, any terms or policies to which they refer, or any applicable law.\nAccount and password</p>\n<p>In order to use certain features of the Site you may be required to create and manage an account on the Site (an \"Account\"). Accounts may be created on the Site by you choosing, or being provided with, a user identification code, password or other piece(s) of information as part of our security procedures (\"Account Information\") to set up and manage your Account.</p>\n<p>You agree that you are solely responsible for keeping your Account Information confidential. You must not disclose your Account Information to any third party. If you know or suspect that anyone other than you knows your Account Information you must promptly notify us.</p>\n<p>We have the right to disable any Account at any time if in our reasonable opinion you have failed to comply with any of the provisions of these terms of use.</p>\n<h3>Business accounts</h3>\n<p>If you establish an Account on behalf of a company, organisation, entity, or brand (a \"Brand\", and such Account a \"Brand Account\"), the terms \"you\" and \"your\", as used throughout the Agreements, apply to both you and the Brand, as applicable.</p>\n<p>If you open a Brand Account, you represent and warrant that you are authorised to grant all rights and licences provided for in these terms of use and to bind the Brand to these terms of use accordingly, including all obligations set out in them.</p>\n<p>If you create an Account as a consumer user and at a later date you wish to use that Account for business purposes (i.e. use it as a Brand Account) then you must tell us by sending an email to <a href=\"mailto:support@doopoll.co\">support@doopoll.co</a>.</p>\n<h3>Submission standards</h3>\n<p>Any information or content you submit to or through our Site must conform to standards of accuracy, decency and lawfulness, which shall be applied in our discretion, acting reasonably. In particular, you warrant that any such communication is lawfully submitted and does not breach our terms of use.</p>\n<p>We will not be responsible, or liable to any third party, for the content or accuracy of any information submitted to or available on the Site that is posted by you or any other user of the Site.</p>\n<p>We have the right to disclose your identity to any third party who is claiming that any information posted or uploaded by you to Site constitutes a violation of their rights, including their Intellectual Property Rights (as defined in clause 10 below) or their right to privacy.</p>\n<h3>Accuracy of information and availability of the Site</h3>\n<p>We may update the Site from time to time, and may change the content at any time. While we try to make sure that the Site is accurate, up-to-date and free from bugs, we cannot promise that it will be. Furthermore, we cannot promise that the Site will be fit or suitable for any purpose.</p>\n<p>The Site is provided on an \"as is\" basis and we make no representations, warranties or guarantees, whether express or implied, that the information on the Site is accurate, complete or up-to-date. This information is not intended to constitute technical, financial or legal advice or any other type of advice and should not be relied on for any purposes. You should obtain professional or specialist advice before taking, or refraining from, any action on the basis of the information on our Site. Any reliance that you may place on the information on the Site is at your own risk.</p>\n<p>While we try to make sure that the Site is available for your use, we do not promise that the Site is available at all times nor do we promise the uninterrupted use by you of the Site. If you have any difficulties using the Site, please contact us.</p>\n<p>Access to the Site is permitted by us in our sole and ultimate discretion. We may suspend, withdraw, discontinue or change all or any part of the Site as we see fit and without notice. We will not be liable to you if for any reason the Site is unavailable at any time or for any period.</p>\n<h3>Hyperlinks and third party sites</h3>\n<p>The Site may contain hyperlinks or references (including banner and pop-up advertising) to third party websites other than the Site. We have no control over third party websites and accept no legal responsibility for any content, material or information contained in them. The display of any hyperlink and reference to any third party website, product or service does not mean that we endorse that third partyâ€™s website, products or services and any reliance you place on such hyperlink, reference or advert is done at your own risk.</p>\n<p>Your use of a third party site may be governed by the terms and conditions of that third party site. It is your responsibility to ensure you are happy with such third party terms and conditions.\nYou may link to the Site from another site provided you do so in a way that is fair and legal and does not damage our reputation or take advantage of it. You must not present or establish a link in such a way as to suggest any form of association, approval or endorsement on our part where none exists. We reserve the right to withdraw linking permission without notice and to require you to immediately remove any link to the Site at any time, and you shall immediately comply with any request by us to remove any such link.</p>\n<h3>Ownership and use of your data</h3>\n<p>You are solely responsible for the accuracy and reliability of any data entered or uploaded to the Site by you while using the Services (\"your Data\").</p>\n<p>You retain ownership of your Data and all rights therein. Nothing in these T&#x26;Cs grants us, or transfers to us, any legal rights in your Data other than as necessary for us to process your Data in accordance with our T&#x26;Cs, for example to provide you with access to the Site and the Services. Subject to the \"Accuracy of information and availability of the Site\" section above, we will only use your Data for these purposes.</p>\n<p>We reserve the right to disclose your Data to law enforcement officials and/or HMRC in the investigation of fraud or other alleged unlawful activities.</p>\n<p>While we try to make sure that the Site is secure, we cannot guarantee the security of any information that you supply to us and therefore we cannot guarantee that it will be kept confidential. For that reason, you should not let us have any information that you regard as confidential, commercially sensitive or valuable. While we value your feedback, you agree not to submit any such information.</p>\n<h3>Ownership, use and intellectual property rights</h3>\n<p>References in these terms of use to \"Intellectual Property Rights\" means copyright, patents, rights in inventions, rights in confidential information, know-how, trade secrets, trade marks, service marks, trade names, design rights, rights in get-up, database rights, rights in data, domain names, rights in computer software and all similar rights of whatever nature and, in each case: (i) whether registered or not, (ii) including any applications to protect or register such rights, (iii) including all renewals and extensions of such rights or applications, (iv) whether vested, contingent or future and (v) wherever in the world they exist.</p>\n<p>This Site and all Intellectual Property Rights in it are owned by us, our licensors or both (as applicable). Such Intellectual Property Rights are protected by copyright laws and treaties around the world. We and our licensors reserve all of our and their rights in any such Intellectual Property Rights in connection with these terms of use. This means, for example, that we and they remain owners of them and free to use them as we and they see fit.</p>\n<p>Nothing in these terms and conditions grants you any legal rights in the Site other than as necessary to enable you to access the Site, and any further or additional use is strictly prohibited unless you have our prior written permission.</p>\n<p>If you copy, download or otherwise use any part of the Site in breach of these terms of use, your right to use the Site will cease immediately and you must, at our option, return or destroy any copies of the materials you have made.</p>\n<h3>Viruses</h3>\n<p>We do not guarantee that the Site will be secure or free from bugs or viruses. You are responsible for configuring your information technology, computer programmes and platform in order to access the Site. You should use your own virus protection software.</p>\n<p>You must not misuse the Site by knowingly introducing viruses, trojans, worms, logic bombs or other material which is malicious or technologically harmful. You must not attempt to gain unauthorised access to the Site, the server on which the Site is stored or any server, computer or database connected to the Site. You must not attack our site via a denial-of-service attack or a distributed denial-of service attack. By breaching this provision, you would commit a criminal offence under the Computer Misuse Act 1990. We will report any such breach to the relevant law enforcement authorities and we will co-operate with those authorities by disclosing your identity to them. In the event of such a breach, your right to use the Site will cease immediately.\nWe will not be liable for any loss or damage caused by a virus, distributed denial-of-service attack, or other technologically harmful material that may infect your computer equipment, computer programs, data or other proprietary material due to your use of the Site or to your downloading of any content on it, or on any website linked to it.</p>\n<h3>User requirements</h3>\n<p>If you are an individual, you may only use the Site if you have the power to form a contract with SurveyMonkey. None of the Sites are intended for use by individuals less than 13 years old. If you are under 13 years old or do not have the power to form a contract with doopoll, you may not use the Sites.</p>\n<p>We recommend that parents and guardians directly supervise any use of the Sites by minors. If you are not an individual, you warrant that you are validly formed and existing under the laws of your jurisdiction of formation, that you have full power and authority to enter into these Terms, and that you have duly authorised your agent to bind you to these Terms. You represent and warrant that you will comply with all laws and regulations applicable to your use of the Sites.</p>\n<h3>Our rights</h3>\n<p>We reserve the right to:</p>\n<ul>\n<li>modify or withdraw, temporarily or permanently, the Site (or any part thereof) with or without notice to you and we shall not be liable to you or any third party for any modification to or withdrawal of the Site; and</li>\n<li>prevent or suspend your access to the Site if you do not comply with any part of these terms and conditions, any terms or policies to which they refer, or any applicable law; and</li>\n<li>change these terms and conditions (and any terms or policies to which they refer) from time to time, and your continued use of the Site (or any part thereof) following such change shall be deemed to be your acceptance of such change. It is your responsibility to check regularly to determine whether these terms and conditions or any related documents have been changed. If you do not agree to any change to these terms and conditions or any related documents then you must immediately stop using the Site.</li>\n</ul>\n<h3>Limitation of our liability</h3>\n<p>Nothing in these terms of use excludes or limits our liability for death or personal injury arising from our negligence, or our fraud or fraudulent misrepresentation, or any other liability that cannot be lawfully excluded or limited.</p>\n<p>Subject to the above, and to the extent permitted by law, we exclude all conditions, warranties and/or representations, whether express or implied, which may apply to the Site or any Content on it.</p>\n<p>Subject to the above, we will not be liable to any user of the Site for any loss or damage, whether in contract, tort (including negligence), breach of statutory duty, or otherwise, even if foreseeable, arising under or in connection with the use of, or inability to use, the Site, or with reliance on any content displayed on the Site. In particular, we will not be liable for any business interruption, for any loss of profits, sales, business, revenue, anticipated savings, business opportunity, goodwill or reputation, or for any indirect or consequential loss or damage.</p>\n<h3>Data protection</h3>\n<p>You and we shall provide each other with reasonable assistance in complying with our obligations under applicable data protection law (including the Data Protection Act 1998 and the Privacy and Electronic Communications (EC Directive) Regulations 2003) insofar as necessary to facilitate each of our compliance with these terms of use. Information about how we collect and process user data is set out in our privacy and cookie policy at <a href=\"http://doopoll.co/legals/privacy-policy\">http://doopoll.co/legals/privacy-policy</a>.</p>\n<h3>General legal terms</h3>\n<p>Events beyond our control â€“ We shall have no liability to you for any breach of these terms caused by any event or circumstance beyond our reasonable control including, without limitation, strikes, lock-outs or other industrial disputes; breakdown of systems or network access; or flood, fire, explosion or accident.</p>\n<p>Rights of third parties â€“ No one other than a party to these terms of use has any right to enforce any of these terms of use.</p>\n<p>Variation â€“ These terms of use are dated 11.05.2016. No changes to these terms are valid or have any effect unless agreed by us in writing. We reserve the right to vary these terms of use from time to time. Our new terms will be displayed on the Site and by continuing to use and access the Site following such changes, you agree to be bound by any variation made by us. It is your responsibility to check these terms of use from time to time to verify such variations.</p>\n<p>Breach â€“ We shall apply these terms of use in our absolute discretion. In the event of your breach of any of these terms we may terminate or suspend your use of the Site, remove or edit Submissions, disclose Submissions to law enforcement authorities or take any action we consider necessary to remedy the breach.</p>\n<h3>Disputes</h3>\n<p>We will try to resolve any disputes with you quickly and efficiently.\nIf you are unhappy with us under these terms of use please contact us as soon as possible by sending an email to <a href=\"mailto:support@doopoll.co\">support@doopoll.co</a>.</p>\n<p>If you and we cannot resolve a dispute using our internal complaint handling procedure, we will let you know that we cannot settle the dispute with you, and give you certain information required by law about the options available to you.</p>\n<h3>Applicable law</h3>\n<p>If you are a consumer, please note that these terms of use, their subject matter and formation, are governed by the law of England and Wales. You and we both agree to that the courts of England and Wales will have exclusive jurisdiction. However, if you are a resident of Northern Ireland you may also bring proceedings in Northern Ireland, and if you are resident of Scotland, you may also bring proceedings in Scotland.</p>\n<p>If you are a business, these terms of use, their subject matter and formation (and any non-contractual disputes or claims) are governed by the law of England and Wales. We both agree to the exclusive jurisdiction of the courts of England and Wales.</p>","fields":{"slug":"/legals/terms-of-use/"},"frontmatter":{"title":"Terms of Use","lastUpdated":"10.10.2017"}}},"pathContext":{"slug":"/legals/terms-of-use/"}}

